import axiosInstance from "@/js/common/axiosInstance";
import {AxiosResponse} from "axios";
import newsImage from '@/assets/img/town.png'

class News {
    img: string;
    title: string;
    text: string;
    createdAt?: string;

    constructor(img:string, title:string, text:string, createdAt?: string) {
        this.img = img;
        this.title = title;
        this.text = text;
        this.createdAt = createdAt;
    }

    /* eslint-disable */
    static get(options?:any): Promise<AxiosResponse<News[]>> {
        return new Promise<AxiosResponse<News[]>>((resolve) => {
            resolve({
                data: [
                    new News(newsImage, '  \n' +
                        '  \n' +
                        ' ', '    ,        ,       .\n' +
                        '\n' +
                        '          ,  , , , ,   ,    ,     .'
                    ),
                    new News(newsImage, '  \n' +
                        '  \n' +
                        ' ', '    ,        ,       .\n' +
                        '\n' +
                        '          ,  , , , ,   ,    ,     .'
                    ),
                    new News(newsImage, '  \n' +
                        '  \n' +
                        ' ', '    ,        ,       .\n' +
                        '\n' +
                        '          ,  , , , ,   ,    ,     .'
                    )
                ]
                } as AxiosResponse<News[]>
            )
        })
    }
}

export default News